.hero {
  height: 100vh;
  width: 100%;
  background-color: rgb(0, 0, 0);
  position: fixed;
  top: 4rem;
  left: 8rem;
  border-radius: 0.3rem;
  color: white;
}
span.hero-heading {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  font-size: 6rem;
  font-weight: 800;
  margin: 3rem;
  display: block;
}
span.hero-para {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  font-size: 4rem;
  font-weight: bold;
  margin: 3rem;
  color: rgb(122, 199, 0, 6);
}
span.hero-para2 {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  font-size: 4rem;
  font-weight: bold;
  margin: 3rem;
  color: rgb(122, 199, 0, 6);
  display: flex;
  justify-content: center;
  align-items: center;
}
span.hero-text {
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  flex-direction: column;
  margin-top: 10rem;
}
button.btn.btn-success {
  height: 3.5rem;
  width: 10rem;
  background-color: rgb(122, 199, 6);
  transform: scale(1.2);
  border: none;
  margin-left: 3rem;
}

button.btn.btn-success:hover {
  background-color: rgb(67, 67, 67);
  color: white;
  transition: all 1s;
  border: none;
}
